import { StorageType, AllRes, StoreConfig } from './types'
import { serialize, deserialize } from './helps'
class Store {
  storage: any

  constructor(type:StorageType) {
    this.storage = window[type]
  }
  do(config: StoreConfig):any {
    // this[config.method](config.key,config.value)
    switch (config.method) {
      case 'get':
          this.get(config.key)
          break
      case 'set':
          this.set(config.key,config.value)
          break
      case "clear":
          this.clear()
           break
      case "getAll":
           this.getAll()
           break
      case "has":
           this.has(config.key)
           break
      case "remove":
           this.has(config.key)
           break
      default:
           this.get(config.key)

    }
  }
  // 设置storage
  set(key:string|undefined,value?:any): any {
    if (!key) return null
    if (value === undefined) {
      this.remove(key)
    }
    this.storage.setItem(key, serialize(value))
    return value
  }
  // 获取storage
  get(key:string|undefined): any {
    if (!key) return undefined
    let val = deserialize(this.storage.getItem(key))
    return val === undefined || val === null ? undefined : val
  }

  // 是否存在这个key
  has(key:string|undefined): boolean {
    return this.get(key) !== undefined
  }

  // 移除这个key
  remove(key:string|undefined): void {
    this.storage.removeItem(key)
  }
  // 清除storage
  clear(): void {
    this.storage.clear()
  }
  // 获取所有的storage
  getAll(): object {
    let ret: AllRes
    ret = {}
    this.forEach((key: string, val: any) => {
      ret[key] = val
    })
    return ret
  }
  // 遍历
  forEach(callback: any) {
    for (let i = 0; i < this.storage.length; i++) {
      let key = this.storage.key(i)
      callback(key, this.get(key))
    }
  }
}

export default Store
